#containerLunetteHome {
    background-color: $color-2;


    #imageHome {
        width: 100%;
    
    }


    #containerEnsembleText{
        position:relative;   
        margin-left: 3%;

        h2{
            font-weight: 400;
            font-size: 2.2em;
          
        }
        p{
            line-height: 0.5;
            font-weight: 300;
            font-size: 1em;
        }
        #containerBtnTextLunetteHome {
            display: flex;
            justify-content: space-between;
            width: 420px;

            #btnSunglass, #btnEyeglass {
                font-weight: 900;
                font-size: 1.1em;
                margin-bottom: 60px;
                line-height: 7.0;
                cursor: pointer;

            }

            .flecheHome{
                font-size: 1.4em;
                position: relative;
                top: 6px;
                cursor: pointer;
            }

        }
    
     
    }








}


@media only screen and (max-width:480px){


    #containerLunetteHome{
      
      width: 134%;
  
      
  
  
  
  
  
  
  
  
  
  
  
    }
   }





