#containerVedetteHome {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    


    h2{
        margin-top: 150px;

        color: $color-3;
    }


    #containerVedetteImage{
        display: flex;


        img {
            width: 100%;
            cursor: pointer;
        }
    }

    .infoVedette {
        display: flex;
    }


    .infoVedette {
        display: flex;
        justify-content: space-around;
        margin: 0 13%;
        margin-bottom: 80px;
       


        .likeVedette{
            margin-left: auto;
            font-size: 1.8em;

            :active{
                background-color: black;
            }
            
            :hover {
                color: $color-1
            }
         
        }

        .modelVedette {
            font-weight: 500;
        }

    }





}

@media only screen and (max-width:480px){


    #containerVedetteHome{
      
      width: 134%;
  
      #containerVedetteImage{
          flex-direction: column;
      }
  
  
  
  
  
  
  
  
  
  
  
    }
   }



