 #containerNavigation{
    display: flex;
    justify-content: flex-end;
    background-color: $color-1;

    #mobilNav{
      margin-top: 2%;
      margin-bottom: 2%;
      margin-left: -4%;
      margin-right: auto;
      font-weight: 600;
      display: none;
      font-size: 1.5em;

    }
 

    a{
        color: $color-3;
    }
    

    #premierNav {
      margin-right: auto;
      margin-left: 3%;
      margin-top: 2%;
      margin-bottom: 2%;
      width: 300px;
      display: flex;
      justify-content: space-between;
      font-weight: 400;


    }


    #deuxiemeNav {
      margin-top: 2%;
      margin-bottom: 2%;
      margin-left: -4%;
      margin-right: auto;
      font-weight: 600;
   
      

    }

    #iconNav {
      margin-top: 2%;
      margin-bottom: 2%;
      margin-right: 3%;
      width: 150px;
      display: flex;
      justify-content: space-between;
      font-size: 1.5em;
      
    }



 }



 @media only screen and (max-width:480px){


  #containerNavigation{
    
    width: 134%;
    margin-bottom: -1%;




    #premierNav{
      display: none;
    }

    .iconHide {
      display: none;
     
    }


    #mobilNav{
      display: inline-block;
      margin-left: 5%;
      
    }

    #deuxiemeNav {
     position: relative;
     left: 70px;
      

    }
 
 

    #shopIcon{
      left: 120px;
      position: relative;
    }





  }
 }