#containerNewArrival {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;


    h2{
        margin-top: 160px;
        margin-bottom: 80px;
        font-size: 1.8em;
        font-weight: 500;
    }

    #containerProfil{
        width: 100%;
        display: flex;
        
        
    .containerImageText{

        display: flex;
        align-items: center;
        flex-direction: column;
        
    }

  
    .arrivalImage {

        width: 80%;
    }

    .containerTexte{
        margin-top: 15px;
        margin-bottom: 160px;

        width: 80%;
        display: flex;
        justify-content: flex-end;
        .modelArrival{

            margin-right: auto;
        }

        .likeArrival {
            font-size: 1.6em;
        }

    }

}
}




@media only screen and (max-width:480px){


    #containerNewArrival{
      
      width: 134%;

  
  
      #containerProfil{
          flex-direction: column;
      }
  
  
  
  
  
  
  
  
  
    }
   }