#containerNewsLetter{
display: flex;
flex-direction: column;
align-items: center;
background-color: $color-3;
margin-top: -10px;

h2{
    color: $color-4;
    margin-top: 70px;
    font-size: 1.3em;

}

p{
    color: $color-4;
    font-size: 0.8em;
    margin-bottom: 30px;
}

#ContainerBtnField {
    width: 33%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;

    input {
        width: 45%;
        padding-left: 3%;
        border: 2px solid $color-4;
        background-color: $color-3;
        color: $color-4;
        font-family: 'Roboto Slab';

    }

    button {

        width: 48%;
        height: 30px;
        border: 2px solid $color-4;
        background-color: $color-4;
        color: $color-3;
        font-family: 'Roboto Slab';


    }

    


}











}

@media only screen and (max-width:480px){


    #containerNewsLetter{
      
      width: 134%;
  
      p{
        text-align: center;
        margin: 0px 30px;
        margin-bottom: 50px;
        font-weight: 200;
    }
  
  
  #ContainerBtnField{
      flex-direction: column;

      input {
        width: 139px;
        height: 30px;
       

    }

    button {

    width: 150px;
    

    }
  }
  
  
  
  
  
  
  
  
    }
   }