#containerFooter{
    background-color: $color-2;
    display: flex;
    flex-direction: column;
 

    #containerContainerText {
        display: flex;
    justify-content: space-around;
    margin-top: 40px;
    margin-bottom: 10px;

    p{
        font-weight: 400;
        font-size: 0.9em;
    }
    h3{
        font-size: 1em;

    }
       
    }

    #containerIcons{
        
        display: flex;
        align-items: center;
        justify-content: center;
        
        span {
            margin: 0px 1%;
        }
    }

    h5{
        font-size: 0.6em;
        text-align: center;
        margin-top: 40px;
        margin-bottom: 20px;
    }
}


@media only screen and (max-width:480px){


    #containerFooter{
      
      width: 134%;
  
      p{
          display: none;
      }

      #containerContainerText {
          flex-direction: column;
          justify-content: center;
          align-items: center;
          margin-bottom: 50px;

      }

      #containerIcons{
      span {
        margin: 0px 6%;
    }
   
}
  
  
  
  
  
  
  
    }
   }